<script>
import { mapGetters } from 'vuex'

export default {
    name: 'activity-index',

    data() {
        let list = [];

        return {
            list: list,
            activeShow: { },
            hoActivityDialogVisible: false,
            fsActivityDialogVisible: false,
            lhdActivityDialogVisible: false,


            pkcpcgjlActivityDialogVisible: false,
            zrlysdlActivityDialogVisible: false,
            dzqpjyjActivityDialogVisible: false,
            wzdzqpActivityDialogVisible: false,
            
            bghbActivityDialogVisible: false,
        }
    },

    computed: {
		user() { return this.$store.getters.user },
		app() { return this.$store.getters.app },
        product() { return this.$store.getters.product },
    },

    mounted() {
        this.init();
    },

    methods: {
        init() {
            this.$store.dispatch('getConfig', 'activity').then(list => {
                this.list = list;

                if(this.$route.query.show) {
                    this.showDetail({
                        id: this.$route.query.show,
                    })
                }
            });
