                //     img: '/images/2019/08/27/ad5eda14-d9ca-4952-931d-242691dc97a7.jpg',
                // },
            // );

            
       },

        showDetail(item) {
           if(item.id == 'sc') {
            this.$store.dispatch('openScActivityDialog');
           }else if(item.id == 'hightodds') {
               this.hoActivityDialogVisible = true;
           }else if(item.id == 'fs') {
               this.fsActivityDialogVisible = true;
           }else if(item.id == 'lhd') {
               this.lhdActivityDialogVisible = true;
           }else if(item.id == 'pkcpcgjl') {
               this.pkcpcgjlActivityDialogVisible = true;
           }else if(item.id == 'zrlysdl') {
               this.zrlysdlActivityDialogVisible = true;
           }else if(item.id == 'dzqpjyj') {
               this.dzqpjyjActivityDialogVisible = true;
           }else if(item.id == 'wzdzqp') {
               this.wzdzqpActivityDialogVisible = true;
           }else if(item.id == 'bghb') {
               this.bghbActivityDialogVisible = true;
           }

        //    this.$set(activeShow, item.id, !activeShow[item.id])
       },
       
        objSpanMethod({ row, column, rowIndex, columnIndex }) {
            if (columnIndex === 0) {
              if (rowIndex % 3 === 0) {
                return {
                  rowspan: 3,
                  colspan: 1
                };
              } else {
                return {
                  rowspan: 0,
                  colspan: 0
                };
              }
            }
        }
    }
}
</script>

